"use client";

import { useState, useEffect } from "react";
import { Button, Typography, Box } from "@mui/material";
import { PollStoreType } from "@/types/poll.types";

const VotePage = (id: string) => {
  const { poll, GetPool } = pollStore() as PollStoreType;
  const [selectedOption, setSelectedOption] = useState<string | null>(null);
  let pollQuestion: string;
  let options: string[];

  useEffect(() => {
    if (id) {
      GetPool(id);
      pollQuestion = poll?.question || "Loading...";
      options = poll?.options || [];
    }
  }, [id, GetPool]);

  const handleVote = (option: string) => {
    setSelectedOption(option);
    // TODO: send vote to backend
  };

  return (
    <Box className="flex flex-col items-center justify-center min-h-[80vh] text-white px-4">
      {/* Question */}
      <Typography
        variant="h4"
        className="p-8 text-center font-bold text-yellow-400"
      >
        {pollQuestion}
      </Typography>

      {/* Options */}
      <Box className="grid grid-cols-2  justify-around space-x-5 space-y-6 w-full max-w-md">
        {options.map((option) => (
          <Button
            key={option}
            variant={selectedOption === option ? "contained" : "outlined"}
            onClick={() => handleVote(option)}
            fullWidth
            sx={{
              margin: "8px",
              borderRadius: "12px",
              padding: "16px",
              fontSize: "18px",
              borderColor: "#ccc",
              color: "#fff",
              backgroundColor:
                selectedOption === option ? "#facc15" : "transparent",
              "&:hover": {
                backgroundColor: "#facc15",
                color: "#000",
              },
            }}
          >
            {option}
          </Button>
        ))}
      </Box>
    </Box>
  );
};

export default VotePage;
